
import type { ReactNode } from 'react';
import { redirect } from 'next/navigation';
import { getUserSession } from '@/app/actions';
import { SidebarProvider, Sidebar, SidebarTrigger, SidebarHeader, SidebarContent, SidebarMenu, SidebarMenuItem, SidebarMenuButton } from '@/components/ui/sidebar';
import { Home, PlusSquare, ListChecks, User, Shield, ArrowLeftToLine } from 'lucide-react';
import Link from 'next/link';
import { Button } from '@/components/ui/button';

export default async function DashboardLayout({
  children,
}: {
  children: ReactNode;
}) {
  const user = await getUserSession();

  if (!user) {
    redirect('/login?message=请先登录以访问仪表盘');
  }

  return (
    <SidebarProvider defaultOpen>
      <div className="flex min-h-[calc(100vh-80px)] bg-muted/40"> {/* Adjusted min-height, considering footer */}
        <Sidebar collapsible="icon" className="border-r hidden md:flex" side="left">
          <SidebarHeader className="p-3 flex flex-col items-center justify-center group-data-[collapsible=icon]:justify-center">
            <div className="mb-3 w-full">
              <Button asChild variant="ghost" className="w-full justify-start group-data-[collapsible=icon]:justify-center group-data-[collapsible=icon]:w-auto group-data-[collapsible=icon]:aspect-square">
                <Link href="/">
                  <ArrowLeftToLine className="size-5 text-primary" />
                  <span className="group-data-[collapsible=icon]:hidden ml-2 text-sm text-primary">返回首页</span>
                </Link>
              </Button>
            </div>
            <User className="size-7 text-primary hidden group-data-[collapsible=icon]:block mb-1" aria-label="用户仪表盘"/>
            <h2 className="text-xl font-semibold text-primary group-data-[collapsible=icon]:hidden">用户中心</h2>
          </SidebarHeader>
          <SidebarContent className="pt-4">
            <SidebarMenu>
              <SidebarMenuItem>
                {/* Changed Link to a for testing */}
                <SidebarMenuButton asChild tooltip="仪表盘首页" className="justify-start" isActive={true}>
                  <a href="/dashboard" className="flex items-center w-full h-full"> {/* Ensure 'a' tag takes full space if needed by styles */}
                    <Home className="size-5" />
                    <span className="group-data-[collapsible=icon]:hidden ml-2">仪表盘首页</span>
                  </a>
                </SidebarMenuButton>
              </SidebarMenuItem>
              <SidebarMenuItem>
                <SidebarMenuButton asChild tooltip="提交新作品" className="justify-start">
                  <Link href="/dashboard/submit">
                    <PlusSquare className="size-5" />
                    <span className="group-data-[collapsible=icon]:hidden ml-2">提交新作品</span>
                  </Link>
                </SidebarMenuButton>
              </SidebarMenuItem>
              <SidebarMenuItem>
                 <SidebarMenuButton asChild tooltip="我的作品" className="justify-start">
                  <Link href="/dashboard/my-projects">
                    <ListChecks className="size-5" />
                    <span className="group-data-[collapsible=icon]:hidden ml-2">我的作品</span>
                  </Link>
                </SidebarMenuButton>
              </SidebarMenuItem>
              {user.type === 'staff' && (
                <SidebarMenuItem>
                  <SidebarMenuButton asChild tooltip="管理后台" className="justify-start">
                    <Link href="/admin">
                      <Shield className="size-5" />
                      <span className="group-data-[collapsible=icon]:hidden ml-2">管理后台</span>
                    </Link>
                  </SidebarMenuButton>
                </SidebarMenuItem>
              )}
            </SidebarMenu>
          </SidebarContent>
        </Sidebar>

        <div className="flex flex-col flex-1">
          <header className="sticky top-0 z-30 flex h-14 items-center gap-4 border-b bg-background px-4 sm:static sm:h-auto sm:border-0 sm:bg-transparent sm:px-6 md:hidden">
            <SidebarTrigger />
            <h1 className="text-xl font-semibold flex-1 text-center">
              用户中心
            </h1>
          </header>
          <main className="flex-1 p-4 sm:px-6 sm:py-0 overflow-auto">
            <div className="py-4 md:py-6"> {/* Added more padding for desktop */}
              {children}
            </div>
          </main>
        </div>
      </div>
    </SidebarProvider>
  );
}
